/* Mixin */
@mixin vertical-align {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

@mixin modal-label {
  font-weight: bold;
  font-size: 14px;
  margin: 0 0 8px;
  padding: 0;
  display: block;
  position: relative;
}

@mixin field-description {
  font-size: 13px;
  padding: 5px 0 0;
  margin: 0;
  line-height: 18px;
  font-style: italic;
  color: #aaaaaa;
}

@mixin clearfix {
  &:after {
	content: "";
	display: table;
	clear: both;
  }
}

@mixin easing-transition {
  -webkit-transition: 0.2s all linear;
  	-moz-transition: 0.2s all linear;
  	-o-transition: 0.2s all linear;
  	transition: 0.2s all linear;
}

$blue: #42A4F5;
$orange: #FAA627;